@use '../base/mixins' as m;
@use '../base/variables' as v;

.gallery{
    margin-top: v.$sp;
    h3{
        text-align: center;
    }
}
.image-list{
    @include m.grid(2, v.$sp);
    @include m.phone {
        @include m.grid(3, v.$sp)
    };
    @include m.tablet {
        @include m.grid(4, v.$sp)
    }
    @include m.desktop {
        @include m.grid(6, v.$sp)
    }
    picture:hover{
        transform: scale(1.1);
        cursor: pointer;
    }
}
.imageContainer{
    position: fixed;
    top: 0;
    right: 0;
    z-index: 20;
    height: 100vh;
    width: 100vw;
    background: rgb(0 0 0 / .5);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: v.$sp2;
    img{
        width: 80%;
        max-width: 80rem;
    }
    button{
        width: 4rem;
        height: 4rem;
        border: none;
        border-radius: 50%;
        background-color: v.$white;
        color: v.$black;
        font-weight: bolder;
        &:hover{
            transform: scale(1.1);
            cursor: pointer;
        }
    }
}
.showingImage{
    overflow-y: hidden;
}
