@use '../base/variables' as v;
@use '../base/mixins' as m;

.wellcomer{  
    position: relative;
    .overlay{
        width: 100%;
        height: 100%;
        position: absolute;
        display: flex;
        align-items: center;
        
        background: rgb(65,88,208);
        background: linear-gradient(90deg, rgba(65,88,208,0.50) 0%, rgba(200,80,192,0.50) 44%, rgba(255,204,112,0.5) 100%);
    }
    
    video{
        display: block;
        object-fit: cover;
        width: 100%;
        min-height: 50rem;
    }
}
.overlay_text{
    z-index: 2;
    @include m.desktop {
        h2{
            font-size: 8rem;
        }
        p{
            font-size: 2rem;
        } 
    }
    @include m.tablet {
        h2{
            font-size: 7.5rem;
        }
        p{
            font-size: 1.9rem;
        }
    }
    h2, p{
        color: v.$white;
    }
    h2{
        font-size: 7rem;
        margin: 0;
    }
    p{
        display: inline-block;
        padding: 1rem 2rem;
        background-color: v.$red;
        font-weight: 700;
        font-size: 1.8rem;
    }
}