@use '../base/variables' as v;
@use '../base/mixins' as m;

.lineup{
    background-color: v.$red;
    padding: v.$sp2 0;
    .day{
        margin: 3rem auto;
        text-transform: uppercase;
    }
    p, h3{
        color: v.$white;
        text-align: center;
    }
}

.lineup_container{
    @include m.phone {
        @include m.grid(2, v.$sp2)
    }
}

.lineup_card{
    p{
        color: v.$black;
        @include m.phone {
            margin-top: 0rem;
        }
        margin-top: v.$sp2;
        padding-top: v.$sp;
        &::after{
            content: '';
            display: block;
            width: 20rem;
            height: 10rem;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: 15rem;
            margin: 0 auto;
        }
    }   
}
.rock p::after{
    background-image: url('../img/icono_rock.png');
}
.edm p::after{
    background-image: url('../img/icono_edm.png');
}

.schedule{
    li{
        padding: v.$sp;
        span{
            font-weight: bold;
        }
    }
}

.bg-yellow{
    background-color: v.$yellow;
    li:nth-child(odd){
        background-color: darken($color: v.$yellow, $amount: 10);
    }
}
.bg-green{
    background-color: v.$green;
    li:nth-child(odd){
        background-color: darken($color: v.$green, $amount: 10);
    }
}