@use './variables' as v;

@mixin phone(){
    @media (min-width: v.$phone) {
        @content
    }
};

@mixin tablet(){
    @media (min-width: v.$tablet) {
        @content
    }
};

@mixin desktop(){
    @media (min-width: v.$desktop) {
        @content
    }
};

@mixin desktopXL(){
    @media (min-width: v.$desktopXL) {
        @content
    }
};

@mixin grid($colums, $gap){
    display: grid;
    grid-template-columns: repeat($colums, 1fr);
    gap: $gap;
}