html {
  font-size: 65.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

img {
  width: 100%;
  height: auto;
}

p {
  font-size: 2.4rem;
  color: #0f0f0f;
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.header {
  background-color: #4CBBB3;
}
@media (min-width: 992px) {
  .header h1 {
    margin: 2rem 0;
  }
}
.header h1 {
  color: #f9f4f4;
  text-align: center;
}

.header_content {
  display: block;
  max-width: none;
}
@media (min-width: 992px) {
  .header_content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.header_nav {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  row-gap: 2rem;
  column-gap: 2rem;
}
@media (min-width: 992px) {
  .header_nav {
    max-width: 40rem;
    flex-grow: 0.5;
  }
  .header_nav a {
    font-size: 2rem;
  }
}
.header_nav a {
  color: #f9f4f4;
  font-size: 2.4rem;
}
.header_nav a:hover {
  color: #FEF45D;
}

.barSticky {
  position: sticky;
  top: 0;
  z-index: 10;
  width: 100%;
  box-shadow: #0f0f0f 0rem 0.3rem 1rem;
}

.wellcomer {
  position: relative;
}
.wellcomer .overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  align-items: center;
  background: rgb(65, 88, 208);
  background: linear-gradient(90deg, rgba(65, 88, 208, 0.5) 0%, rgba(200, 80, 192, 0.5) 44%, rgba(255, 204, 112, 0.5) 100%);
}
.wellcomer video {
  display: block;
  object-fit: cover;
  width: 100%;
  min-height: 50rem;
}

.overlay_text {
  z-index: 2;
}
@media (min-width: 992px) {
  .overlay_text h2 {
    font-size: 8rem;
  }
  .overlay_text p {
    font-size: 2rem;
  }
}
@media (min-width: 768px) {
  .overlay_text h2 {
    font-size: 7.5rem;
  }
  .overlay_text p {
    font-size: 1.9rem;
  }
}
.overlay_text h2, .overlay_text p {
  color: #f9f4f4;
}
.overlay_text h2 {
  font-size: 7rem;
  margin: 0;
}
.overlay_text p {
  display: inline-block;
  padding: 1rem 2rem;
  background-color: #F53756;
  font-weight: 700;
  font-size: 1.8rem;
}

.about {
  margin: 4rem auto;
}
@media (min-width: 768px) {
  .about {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.about_info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 2rem;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .about_info {
    margin-top: 0;
  }
}
.about_info h2 {
  margin: 0;
}
.about_info small {
  font-weight: 700;
  font-size: 1.8rem;
  color: #F53756;
}
.about_info p {
  font-size: 2rem;
}

.about_image {
  min-width: 280px;
}

.lineup {
  background-color: #F53756;
  padding: 4rem 0;
}
.lineup .day {
  margin: 3rem auto;
  text-transform: uppercase;
}
.lineup p, .lineup h3 {
  color: #f9f4f4;
  text-align: center;
}

@media (min-width: 480px) {
  .lineup_container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

.lineup_card p {
  color: #0f0f0f;
  margin-top: 4rem;
  padding-top: 2rem;
}
@media (min-width: 480px) {
  .lineup_card p {
    margin-top: 0rem;
  }
}
.lineup_card p::after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 15rem;
  margin: 0 auto;
}

.rock p::after {
  background-image: url("../img/icono_rock.png");
}

.edm p::after {
  background-image: url("../img/icono_edm.png");
}

.schedule li {
  padding: 2rem;
}
.schedule li span {
  font-weight: bold;
}

.bg-yellow {
  background-color: #FEF45D;
}
.bg-yellow li:nth-child(odd) {
  background-color: #fef12a;
}

.bg-green {
  background-color: #4CBBB3;
}
.bg-green li:nth-child(odd) {
  background-color: #3a9a93;
}

.gallery {
  margin-top: 2rem;
}
.gallery h3 {
  text-align: center;
}

.image-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .image-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .image-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .image-list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}
.image-list picture:hover {
  transform: scale(1.1);
  cursor: pointer;
}

.imageContainer {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 20;
  height: 100vh;
  width: 100vw;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4rem;
}
.imageContainer img {
  width: 80%;
  max-width: 80rem;
}
.imageContainer button {
  width: 4rem;
  height: 4rem;
  border: none;
  border-radius: 50%;
  background-color: #f9f4f4;
  color: #0f0f0f;
  font-weight: bolder;
}
.imageContainer button:hover {
  transform: scale(1.1);
  cursor: pointer;
}

.showingImage {
  overflow-y: hidden;
}

.tickets h3 {
  padding: 2rem 0;
  margin: 2rem;
}
.tickets p,
.tickets h3,
.tickets header,
.tickets footer {
  color: #f9f4f4;
  text-align: center;
}

.tickets_container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .tickets_container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.ticket .ticket-info {
  margin-top: 2rem;
}
.ticket .ticket-info header {
  text-transform: uppercase;
  background-color: #F53756;
  padding: 1rem;
}
.ticket .ticket-info ul li {
  background-color: #f9f4f4;
  padding: 1rem 0.5rem;
}
.ticket .ticket-info ul li:not(:last-child) {
  border-bottom: #e7d3d3 0.1rem solid;
}
.ticket .ticket-info footer {
  background-color: #752F97;
  padding: 1rem;
}

.main-footer {
  background-color: #F53756;
  padding: 1rem;
}
.main-footer p {
  text-align: center;
  color: #f9f4f4;
  font-size: 1.5rem;
}