@use '../base/variables' as v;
@use '../base/mixins' as m;

.header{
    @include m.desktop {
        h1{
            margin: v.$sp 0;
        }
    }
    background-color: v.$green;
    h1{
        color: v.$white;
        text-align: center;
    }

}

.header_content{
    @include m.desktop {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    display: block;
    max-width: none;
}

.header_nav{ 
    @include m.desktop {
        max-width: 40rem;
        flex-grow: .5;
        a{
            font-size: 2rem;
        }
    }
    display: flex;  
    justify-content: space-evenly;
    flex-wrap: wrap;
    row-gap: v.$sp;
    column-gap: v.$sp;
    a{
        color: v.$white;
        font-size: 2.4rem;
        &:hover{
            color: v.$yellow;    
        }
    }       
}

.barSticky{
    position: sticky;
    top: 0;
    z-index: 10;
    width: 100%;
    box-shadow: v.$black 0rem .3rem 1rem;
}