@use '../base/variables' as v;
@use '../base/mixins' as m;

.about {
    @include m.tablet{
        @include m.grid(2,2rem);
    }
    margin: v.$sp2 auto;
}
.about_info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: v.$sp;
    margin-top: v.$sp;
    @include m.tablet {
        margin-top: 0;
    }
    h2{
        margin: 0;
    }
    small{
        font-weight: 700;
        font-size: 1.8rem;
        color: v.$red;
    }
    p{
        font-size: 2rem;
    }
}
.about_image{
    min-width: 280px;
}