@use './variables' as v;

html{
    font-size: 65.5%;
    box-sizing: border-box;
    // scroll-behavior: smooth;
}
*,
*::before,
*::after{
    box-sizing: inherit;
}

body{
    font-family: v.$primary-font;
    font-size: 1.6rem;
}
img{
    width: 100%;
    height: auto;
}

p{
    font-size: 2.4rem;
    color: v.$black;
    margin: 0;
}
a{
    text-decoration: none;
}
ul{
    list-style: none;
    padding: 0;
    margin: 0;
}

h1,h2,h3{
    margin: 0 0 v.$sp 0;
}
h1{
    font-size: 4rem;
}
h2{
    font-size: 3.6rem;
}
h3{
    font-size: 3rem;
}

.container{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
