@use '../base/variables' as v;
@use '../base/mixins' as m;

.tickets{
    h3{
        padding: v.$sp 0;
        margin: v.$sp;
    }
    p, 
    h3, 
    header,
    footer{
        color: v.$white;
        text-align: center;
    }
}
.tickets_container{
    @include m.grid(1,v.$sp);
    
    @include m.phone {
        @include m.grid(2,v.$sp)
    }
}
.ticket{
    .ticket-info{
        margin-top: v.$sp;
        header{
            text-transform: uppercase;
            background-color: v.$red;
            padding: 1rem;
        }
        ul{
            li{
                background-color: v.$white;
                padding: 1rem .5rem;
            }
            li:not(:last-child){
                border-bottom: darken($color: v.$white, $amount: 10) .1rem solid;
            }
        }
        footer{
            background-color: v.$purple;
            padding: 1rem;
        }
    }
}